.card_item{
    transition: 0.5s;
    border-radius: 0px;
    border: none;
    margin: 1rem;
    background-color: #F6F5FB;
}

.card_item:hover{
    margin-top: -0.2rem;
}

.card_item img{
    border: none;
}

.card_body{
    visibility: hidden;
    transition: 0.5s;
}
.card_item:hover .card_body{
    visibility: visible;
    background-color: white;
} 


.card_title:hover{
    color: #f20791;
}