.card_item img{
    height: 4rem;
}

.card_item img:hover{
    color: white;
}

.title_item{
    font-size: 18px;
    color: #3b3663;
    font-weight: 700;
    margin: 20px 0 10px;
    -webkit-transition: .5s;
    transition: .5s;
}

.desc_item{
    margin: 15px 0 0;
    font-size: 15px;
    line-height: 1.7;
    color: #6a6c72;
}

.card_item{
    border: 1px dotted #2629b2;
    transition: 0.5s;
}

.card_item:hover{
    background-color: #089cf1;
    height: 300px;
    width: 300px;
    color: white;
    border: none;
}


.card_item:hover .title_item{
    color: black;
}

.card_item:hover .desc_item{
    color: black;
}