.title{
    font-size: 20px;
    font-weight: 600;
    margin: 0 0 6px;
    color: #202647;
    text-align: center;
}

.content{
    font-size: 14px;
    color: #6a6c72;
    text-align: center;
}

.card_item img{
    border-radius: 0px;
}

.card_item{
    border-radius: 0px;
    margin: 2rem;
}

.card_body{
    background-color: #F5F5FF;
}

.icons_container{
    width: 100%;
    display: flex;
    justify-content: center;
    gap: 5px;
}

.icons_container div{
    height: 30px;
     width: 30px;
 color: #F272BC;
 border: 1px solid #F272BC;
 border-radius: 50%;
 padding:5px;
 cursor: pointer;
 display: flex;
 justify-content: center;
 align-items: center;
}

.icons_container div *{
    height: 12px;

}
.icons_container div:hover{
    color: white;
    background-color: #F272BC;;
}