.about_one span{
    font-size: 14px;
    color: #f20791;
    font-weight: 500;
    text-transform: uppercase;
    display: block;
    margin-bottom: 5px;
    max-width: 100vw;
}

.heading_one{
    font-size: 38px;
    color: #202647;
    font-weight: 700;
    margin: 0 0 25px;
    position: relative;
    padding-bottom: 25px
}



.about_one strong{
    color: #6a6c72;
    font-weight: 500;
}

.about_one p{
    margin: 15px 0 0;
    font-size: 15px;
    line-height: 1.7;
    color: #6a6c72;
}

.about_one{
    padding-top: 100px;
}

.line_one{
    display: inline-block;
    width: 5rem;
    height: 0.2rem;
    background-color: #fab4a2;
}

.line_two{
    display: inline-block;
    width: 2rem;
    height: 0.2rem;
    background-color: #ff724e;
    margin-left: 1rem;
}

.outer_block{
    display: flex;
    flex-direction: row;
}

.outer_block span{
    display: inline-block;
    font-size: 35px;
    margin-left: 5px;
    color: #ff724e;
}

.outer_block h3{
    color: #6a6c72;
}