.jumbotron {
    background-image: url('https://neemo-react.envytheme.com/images/page-banner-bg.jpg');
    height: 25rem;
    width: 100vw;
}

.background{
    background-color: #2629b2;
    width: 100%;
    height: 25rem;
    opacity: 0.8;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.center_box{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


.center_box h4{
    margin-bottom: 0;
    color: #fff;
    font-size: 42px;
    font-weight: 700;
    text-align: center;
 
}

.center_box p{
    color: #fff;
    text-align: center;
    font-weight: bold;
    letter-spacing: 0.1rem;
    padding: 1rem;
    display: inline-block;
}

.dot_icon{
    height: 1rem;
    width: 1rem;
    background-color: #F20791;
    border-radius: 6rem;
    display: inline-block;
    margin: auto;
}

.content{
    display: inline-block;
}

.background input{
    display: block;
    width: 20rem;
    background-color: #fff;
    border: none;
    height: 55px;
    padding-left: 25px;
    border-radius: 70px;
    padding-top: 5px;
    outline: 0;
    color: #202647;
}

.background input{
    margin-left: 2rem;
}


.background button{
    margin-left: 2rem;
}


.background button{
    background-color: #e4045e;
    color: #fff;
    border: none;
    height: 55px;
    padding: 0 30px;
    border-radius: 70px;
    transition: .5s;
    line-height: 45px;
    text-transform: capitalize;
    font-size: 16px;
    font-weight: 500;
}

