.card_item{
    height: 255px;
    width: 370px;
    padding: 0.25rem;
    transition: 0.5s;
    border-radius: 0px;
    border: none;
}

.card_item:hover{
    margin-top: -1rem;
    background-color: #F20791;
}


.card_item p{
    margin: 15px 0 0;
    font-size: 15px;
    line-height: 1.7;
    color: #6a6c72;
}

.card_item:hover .text_item{
    color: white;
}

.card_item:hover .title_item{
    color: white;

}
.title_item{
    text-decoration: none;
    color:#202652
}

a:hover{ 
    text-decoration: underline;
}

.flaticon_landing_page:before {
    content: "\f106";
}

.card_item img{
    height: 2rem;
    margin: 1rem 0.1rem;
}

